#photographer_section {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 70px;
  margin:0;
  padding:0;
  margin-top: 100px;

  li {
    margin:0;
    padding:0;
    list-style: none;

    a {
      @include column();

      text-decoration: none;
      color: inherit;
    }
  }
}

